@import './mixins.scss';
@import './variables.scss';

    .projects {
            padding-top: 2rem;
            max-height: 90vh;
            background-color: $gray;
            margin: auto;
            @include text_generic;
    
            &_title {
                @include title_section;
                margin-bottom: 1rem;
            }
    
            &_list {
                display: grid;
                max-height: 70vh;
                grid-template-columns: 1fr;
                grid-template-rows: 175px;
                overflow-y: scroll;
                overflow-x: hidden;
                padding: 0.5rem 1rem 1rem 1rem;
    
                &_item {
                    max-height: 175px;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    margin: 0.15rem;
    
                    &_figure {
                        @include flex_center;
                        padding: 0.15rem;
    
                        &_img {
                            width: 150px;
                            height: 100px;
                        }
                    }
    
                    &_container {
                        padding: 0.5rem;
    
                        &_title {
                            font-weight: 400;
                            text-transform: capitalize;
                            margin-bottom: 0.5rem;
                            font-size: 0.8rem;
                            text-align: left;
                            color: white;
                        }
    
                        &_text {
                            font-size: 0.7rem;
                            color: white;
                        }
                    }
                }
    
                &_item:hover {
                    transform: scale(1.05);
                    box-shadow: 0px 0px 5px $green;
                    background-color: #191919;
                }
            }
    
        }

@media(min-width: 768px){
    .projects {
            scroll-snap-align: start;
            padding-top: 4rem;
            max-height: 90vh;
            background-color: $gray;
            margin: auto;
            @include text_generic;
    
            &_title {
                @include title_section;
                margin-bottom: 1rem;
            }
    
            &_list {
                display: grid;
                margin: auto;
                max-width: 90vw;
                max-height: 70vh;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 175px;
                overflow-y: scroll;
                overflow-x: hidden;
                padding: 0.5rem 1rem 1rem 1rem;
    
                &_item {
                    max-height: 175px;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    margin: 0.12rem;
    
                    &_figure {
                        @include flex_center;
                        padding: 0.05rem;
    
                        &_img {
                            width: 150px;
                            height: 100px;
                        }
                    }
    
                    &_container {
                        font-size: 0.9rem;
                        padding: 0.25rem;
    
                        &_title {
                            font-weight: 400;
                            text-transform: capitalize;
                            margin-bottom: 0.5rem;
                            font-size: 0.8rem;
                            text-align: left;
                            color: white;
                        }
    
                        &_text {
                            font-size: 0.7rem;
                            color: white;
                        }
                    }
                }
    
                &_item:hover {
                    transform: scale(1.05);
                    box-shadow: 0px 0px 5px $green;
                    background-color: #191919;
                }
            }
    
        }
}
@media(min-width: 1200px){
    .projects {
            scroll-snap-align: start;
            padding-top: 4rem;
            max-height: 100vh;
            background-color: $gray;
            margin: auto;
            max-width: $width-80;
            @include text_generic;
    
            &_title {
                @include title_section;
                margin-bottom: 2rem;
            }
    
            &_list {
                display: grid;
                max-height: 80vh;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 250px;
                overflow-y: scroll;
                padding: 0rem 0rem 1.2rem 3rem;
    
                &_item {
                    max-width: 90%;
                    max-height: 250px;
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    margin-top: 1rem;
    
                    &_figure {
                        @include flex_center;
                        padding: 1rem;
    
                        &_img {
                            width: 250px;
                            height: 145px;
                        }
                    }
    
                    &_container {
                        padding: 1rem;
    
                        &_title {
                            font-weight: 400;
                            text-transform: capitalize;
                            margin-bottom: 1rem;
                            text-align: left;
                            color: white;
                        }
    
                        &_text {
                            font-size: 0.9rem;
                            color: white;
                        }
                    }
                }
    
                &_item:hover {
                    transform: scale(1.1);
                    box-shadow: 0px 0px 10px $green;
                    background-color: #191919;
                }
            }
    
        }
}
