@import './mixins.scss';
@import './variables.scss';

.contact {
    margin-top: 2rem;
    position: relative;

    &_line1 {
        position: absolute;
        width: 2px;
        background-color: $green;
        height: 75%;
        right: 5%;
    }

    &_line2 {
        position: absolute;
        top: 25%;
        height: 2px;
        background-color: $green;
        width: 45%;
        right: 5%;
    }

    &_title {
        @include title_section;
        margin-top: 1rem;
    }

    &_container {
        margin: 1rem auto;
        padding: 0% 2% 2% 5%;
        width: 70%;
        background-color: $black;
        display: flex;
        flex-direction: column;
        position: relative;


        &_git {
            &_link {
                @include text_generic;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 2rem;
                    margin-right: 0.5rem;
                }
            }
        }

        &_mail {
            &_link {
                @include text_generic;
                text-align: left;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 1.2rem;
                    margin-right: 0.5rem;
                }
            }
        }

        &_linkedin {
            &_link {
                @include text_generic;
                display: flex;
                align-items: center;
                text-decoration: none;
                font-size: 0.8rem;

                &_icon {
                    width: 2rem;
                    margin-right: 0.5rem;
                }
            }
        }
    }
    
}

@media(min-width:768px){
    .contact {
        margin-top: 4rem;
        scroll-snap-align: start;
        position: relative;

        &_line1 {
            position: absolute;
            width: 2px;
            background-color: $green;
            height: 75%;
            right: 10%;
        }

        &_line2 {
            position: absolute;
            top:25%;
            height: 2px;
            background-color: $green;
            width: 45%;
            right: 10%;
        }

        &_title {
            @include title_section;
            margin-top: 4rem;
        }

        &_container {
            margin: 2rem auto;
            padding: 0% 2% 2% 5%;
            width: 50%;
            background-color: $black;
            display: flex;
            flex-direction: column;
            position: relative;
            

            &_git {
                &_link {
                    @include text_generic;
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    font-size: 0.9rem;

                    &_icon {
                        width: 2.5rem;
                        margin-right: 1rem;
                    }
                }
            }

            &_mail {
                &_link {
                    @include text_generic;
                    text-align: left;
                    display: flex;
                    font-size: 0.9rem;
                    align-items: center;
                    text-decoration: none;

                    &_icon {
                        width: 1.7rem;
                        margin-right: 1rem;
                    }
                }
            }

            &_linkedin {
                &_link {
                    @include text_generic;
                    font-size: 0.9rem;
                    display: flex;
                    align-items: center;
                    text-decoration: none;

                    &_icon {
                        width: 2.5rem;
                        margin-right: 1rem;
                    }
                }
            }
        }

    }
}
